import appTabNav from "../../../../components/basic-component/app-tab-nav/app-tab-nav.vue";

import { mapState } from "vuex";

export default {
    data() {
        return {
            theme: {
                color: '#ff4544'
            },
            tabList: [
                {id:1, name: '出售中'},
                {id:2, name: '售罄'},
                {id:3, name: '仓库中'},
                {id:4, name: '最新'}
            ],
            activeTab: '1',
            keyword: '',
            page: 1,
            id: null,
            more_list: false,
            iphone_x: false,
            status: null,
            page_loading: true,
            sort_type: 0,
            sort: 1,
            is_goods_audit: 1,
            go_status: 1,
            is_sold_out: 0,
            list: [],
            is_switch: false,
            is_delete: false,
            apply_up: false,
            toSearch: false,
            device: null,
            mch_id: 0
        }
    },
    components: {
        "app-tab-nav": appTabNav
    },
    computed: {
        ...mapState({
            appImg: state => state.mallConfig.__wxapp_img.mall,
            appSetting: state => state.mallConfig.mall.setting,
            adminImg: state => state.mallConfig.__wxapp_img.mch,
        })
    },
    methods: {
        toEdit(id) {
            uni.navigateTo({
                url: '/plugins/mch/mch/add-goods/add-goods?id=' + id + '&mch_id=' + this.mch_id
            })
            setTimeout(()=>{
                this.id = null
            })
        },
        toAdd() {
            uni.navigateTo({
                url: '/plugins/mch/mch/add-goods/add-goods?mch_id=' + this.mch_id
            })
        },
        beSearch() {
            if (this.keyword.length == 0) {
                this.toSearch = !this.toSearch
            }else {
                this.toSearch = this.toSearch;
            }
        },
        reload() {
            this.id = null;
            this.getList();
        },
        cancel() {
            this.id = null;
            this.is_delete = false;
            this.is_switch = false;
            this.apply_up = false;
        },
        toDelete(e) {
            this.is_delete = true;
        },
        showMenu(id) {
            this.id = id;
        },
        toSwitch(status,id) {
            let that = this;
            that.id = id;
            if(status == 2) {
                that.apply_up = true;
            }else {
                that.status = status;
                that.is_switch = true;
            }
        },
        tabStatus(e) {
            this.activeTab = e.currentTarget.dataset.id;
            this.getList();
        },
        applyStatus() {
            let that = this;
            that.apply_up = false;
            uni.showLoading({
                title: '申请中...'
            });
            that.$request({
                url: that.$api.mch.apply_status,
                method: 'post',
                data: {
                    mch_id: that.mch_id,
                    id: that.id,
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(function(){
                        that.reload();
                    },1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getList() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            that.list = [];
            that.page = 1;
            if(that.activeTab == '1') {
                that.go_status = 1;
                that.is_sold_out = 0;
            }else if(that.activeTab == '2') {
                that.go_status = 1;
                that.is_sold_out = 1;
            }else if(that.activeTab == '3') {
                that.go_status = 0;
                that.is_sold_out = 0;
            }else if(that.activeTab == '4') {
                that.go_status = 1;
                that.is_sold_out = 0;
            }
            that.$request({
                url: that.$api.mch.goods,
                data: {
                    page: that.page,
                    mch_id: that.mch_id,
                    sort: that.sort,
                    sort_type: that.sort_type,
                    keyword: that.keyword,
                    status: that.go_status,
                    is_sold_out: that.is_sold_out,
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = response.data.list;
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == 10) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        getMore() {
            let that = this;
            uni.showLoading({
                title: '加载中...'
            });
            if(that.activeTab == '1') {
                that.go_status = 1;
                that.is_sold_out = 0;
            }else if(that.activeTab == '2') {
                that.go_status = 1;
                that.is_sold_out = 1;
            }else if(that.activeTab == '3') {
                that.go_status = 0;
                that.is_sold_out = 0;
            }else if(that.activeTab == '4') {
                that.go_status = 1;
                that.is_sold_out = 0;
            }
            that.$request({
                url: that.$api.mch.goods,
                data: {
                    page: that.page,
                    mch_id: that.mch_id,
                    sort: that.sort,
                    sort_type: that.sort_type,
                    keyword: that.keyword,
                    status: that.go_status,
                    is_sold_out: that.is_sold_out,
                }
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    that.list = that.list.concat(response.data.list);
                    that.page++;
                    that.more_list = false;
                    if (response.data.list.length == 10) {
                        that.more_list = true;
                    }
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },
        goods_destroy() {
            let that = this;
            that.is_delete = false;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.mch.destroy,
                data: {
                    mch_id: that.mch_id,
                    id: that.id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(function(){
                        that.reload();
                    },1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                un
                
            });
        },

        goods_switch() {
            let that = this;
            that.is_switch = false;
            uni.showLoading({
                title: '加载中...'
            });
            that.$request({
                url: that.$api.mch.switch_status,
                data: {
                    mch_id: that.mch_id,
                    id: that.id
                },
                method: 'post'
            }).then(response=>{
                uni.hideLoading();
                if(response.code == 0) {
                    uni.showToast({
                        title: response.msg,
                        duration: 1000
                    });
                    setTimeout(function(){
                        that.reload();
                    },1000)
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
            });
        },

        getSetting() {
            let that = this;
            that.$showLoading({
                type: 'global',
                text: '加载中...'
            });
            that.$request({
                url: that.$api.mch.setting
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.is_goods_audit = response.data.setting.is_goods_audit;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onReachBottom: function() {
        if (this.more_list) {
            this.getMore();
        }
    },

    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.mch_id = options.mch_id;
        that.go_status = 1;
        that.is_sold_out = 0;
        that.getSetting();
    },

    onShow(options) {
        let that = this;
        var getInfo = setInterval(function(){
            if(that.mch_id > 0) {
                clearInterval(getInfo);
                that.getList();
            }
        },500)
    }
}
